<template>
    <div class="work">
        <div class="container">
            <div class="row">
                <div class="col-lg-10 offset-lg-1">
                    <div class="work-title">
                        <span>图片</span>
                    </div>
                    <div class="work-content">
                        <div class="row">
                            <div class="col-lg-3" v-for="(image, index) of imageData" :key="index">
                                <div class="image-item" @click="showImgView(image.url)">
                                    <img :src="image.url">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-10 offset-lg-1">
                    <div class="work-title" style="margin-top:-1rem">
                        
                    </div>
                    <div class="work-content">
                        <div class="row">
                            <div class="col-lg-8 offset-lg-2 col-12">
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Bus from "../../bus/index.js"

export default {
    data() {
        return {
            imageData: []
        }
    },
    mounted() {
        this.$axios.get("/static/mock/index.json").then(res => {
            this.imageData = res.data.work.image
        })
    },
    methods: {
        showImgView: function(url) {
            Bus.$emit("toggle", url)
        }
    }
}
</script>

<style scoped>
    .work {
        margin: 2rem 0;
    }
    .work-title {
        margin-bottom: 2rem;
    }
    .work-title span {
        font-weight: bold;
        display: block;
        width: fit-content;
        padding: 8px 40px;
        background: #000;
        color: #fff;
    }
    .work-content {
        margin-bottom: 2rem;
    }
    .image-item {
        background: #f1f1f1;
        padding: 10px;
        width: 100%;
        aspect-ratio: 16 / 9;
        overflow: hidden;
        margin-bottom: 1rem;
        cursor: pointer;
        user-select: none;
    }
    .image-item img {
        display: block;
        margin: auto;
        max-width: 100%;
        max-height: 100%;
    }
    .image-frame {
        display: none;
        width: 100%;
        aspect-ratio: 16 / 9;
        overflow: hidden;
        background: #f1f1f1;
        position: relative;
    }
    .image-frame img {
        display: block;
        margin: auto;
        max-width: 100%;
        max-height: 100%;
    }
    .image-frame .image-info {
        position: absolute;
        width: 100%;
        background: #fff;
        height: fit-content;
        bottom: 0;
        opacity: 0.8;
        padding: 1rem;
    }
    .image-frame .image-info p {
        margin: 0;
    }
</style>


